/*--------------------------------------------------------------
# Normalize
--------------------------------------------------------------*/
html {
	font-family: sans-serif;
	-webkit-text-size-adjust: 100%;
	-ms-text-size-adjust: 100%;
}

body {
	margin: 0;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
	display: block;
}

audio,
canvas,
progress,
video {
	display: inline-block;
	vertical-align: baseline;
}

audio:not([controls]) {
	display: none;
	height: 0;
}

[hidden],
template {
	display: none;
}

a {
	background-color: transparent;
}

a:active,
a:hover {
	outline: 0;
}

abbr[title] {
	border-bottom: 1px dotted;
}

b,
strong {
	font-weight: bold;
}

dfn {
	font-style: italic;
}

h1 {
	font-size: 2em;
	margin: 0.67em 0;
}

mark {
	background: #ff0;
	color: #000;
}

small {
	font-size: 80%;
}

sub,
sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
}

sup {
	top: -0.5em;
}

sub {
	bottom: -0.25em;
}

img {
	border: 0;
}

svg:not(:root) {
	overflow: hidden;
}

figure {
	margin: 1em 40px;
}

hr {
	box-sizing: content-box;
	height: 0;
}

pre {
	overflow: auto;
}

code,
kbd,
pre,
samp {
	font-family: monospace, monospace;
	font-size: 1em;
}

button,
input,
optgroup,
select,
textarea {
	color: inherit;
	font: inherit;
	margin: 0;
}

button {
	overflow: visible;
}

button,
select {
	text-transform: none;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
	-webkit-appearance: button;
	cursor: pointer;
}

button[disabled],
html input[disabled] {
	cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
	border: 0;
	padding: 0;
}

input {
	line-height: normal;
}

input[type="checkbox"],
input[type="radio"] {
	box-sizing: border-box;
	padding: 0;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
	height: auto;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
	-webkit-appearance: none;
}

fieldset {
	border: 1px solid #c0c0c0;
	margin: 0 2px;
	padding: 0.35em 0.625em 0.75em;
}

legend {
	border: 0;
	padding: 0;
}

textarea {
	overflow: auto;
}

optgroup {
	font-weight: bold;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

td,
th {
	padding: 0;
}

/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
optgroup,
textarea {
	color: #404040;
	font-family: Helvetica, Arial, sans-serif;
	font-size: 16.2px;
	font-size: 1.0125rem;
	line-height: 1.5;
}

@media screen and (min-width: 600px) {
	body,
	button,
	input,
	select,
	optgroup,
	textarea {
		font-size: 19.62px;
		font-size: 1.22625rem;
	}
}

h1, h2, h3, h4, h5, h6 {
	clear: both;
	font-family: "Rubik", sans;
	line-height: 1.2;
	font-weight: 700;
}

.entry-title {
	font-size: 1.5em;
}

@media screen and (min-width: 600px) {
	.entry-title {
		font-size: 2em;
	}
}

dfn, cite, em, i {
	font-style: italic;
}

blockquote {
	margin: 2em 0;
	position: relative;
	-moz-border-radius: 0.2em;
	-webkit-border-radius: 0.2em;
	border-radius: 0.2em;
	padding: 10px 20px 20px;
	font-size: 1em;
	font-weight: 300;
	line-height: 1.55;
	background: #DBD6C6;
	color: #393A38;
	hanging-punctuation: first;
}

blockquote p {
	margin: .25em 0;
}

blockquote p:last-child {
	margin-bottom: 0;
}

@media screen and (min-width: 900px) {
	blockquote {
		width: 40%;
		float: left;
		margin: .5em 2em 1em -2em;
	}
}

address {
	margin: 0 0 1.5em;
}

pre {
	background: #eee;
	font-family: "Courier 10 Pitch", Courier, monospace;
	font-size: 16.875px;
	font-size: 1.05469rem;
	line-height: 1.5;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code, kbd, tt, var {
	font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
	font-size: 16.875px;
	font-size: 1.05469rem;
}

abbr, acronym {
	border-bottom: 1px dotted #666;
	cursor: help;
}

mark, ins {
	background: #ccc;
	text-decoration: none;
}

big {
	font-size: 125%;
}

.entry-content ul {
	padding-left: 1em;
}

.entry-content ul li {
	margin-bottom: .5em;
}

.entry-title {
	font-weight: 300;
}

p {
	margin: 1em 0;
}

/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
html {
	box-sizing: border-box;
}

*,
*:before,
*:after {
	/* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see https://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
	box-sizing: inherit;
}

body {
	background: #FFFFFF;
	/* Fallback for when there is no custom background color defined. */
}

hr {
	background-color: #ccc;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

ul, ol {
	padding: 0;
	margin: 0 0 1em 1em;
	list-style-position: outside;
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: bold;
}

dd {
	margin: 0 1.5em 1.5em;
}

img {
	height: auto;
	/* Make sure images are scaled correctly. */
	max-width: 100%;
	/* Adhere to container width. */
}

figure {
	margin: 1em 0;
	/* Extra wide images within figure tags don't overflow the content area. */
}

figure a, figure a:hover {
	border: none;
}

figure img {
	width: 100%;
	height: auto;
}

table {
	border-collapse: collapse;
	margin: 0 0 1.5em;
	width: 100%;
	-moz-border-radius: 1em;
	-webkit-border-radius: 1em;
	border-radius: 1em;
	font-size: .9em;
	overflow-x: auto !important;
}

thread th {
	border-bottom: 2px solid #bbb;
	padding-bottom: .5em;
}

td,
th {
	vertical-align: top;
}

th {
	padding: .4em;
	text-align: left;
}

tr:not(:last-child) {
	border-bottom: 1px solid #eee;
}

td {
	padding: .5em 1em;
}

.table-source {
	font-size: .75em;
	color: gray;
	margin-bottom: 4em;
}

audio {
	z-index: -1;
}

a.wpc-button {
	width: 80px;
	height: 80px;
	-moz-border-radius: 50%;
	-webkit-border-radius: 50%;
	border-radius: 50%;
	border: none;
	z-index: 99;
	display: block;
	position: relative;
	white-space: nowrap;
	clear: both;
	margin-top: 40px;
	margin-bottom: 40px;
	font-size: .8em;
	display: flex;
	align-items: center;
}

a.wpc-button.wpc-complete {
	background-image: url("./img/check.png");
	background-image: none, url("./img/check.svg");
	background-repeat: no-repeat;
	background-repeat: no-repeat;
	background-position: center center;
	background-size: 26px 20px;
}

a.wpc-button.wpc-completed {
	background-image: url("./img/check.png");
	background-image: none, url("./img/check.svg");
	background-repeat: no-repeat;
	background-repeat: no-repeat;
	background-position: center center;
	background-size: 26px 20px;
	opacity: .25;
}

a.wpc-button .wpc-inactive, a.wpc-button .wpc-active {
	margin-left: 100%;
	margin-bottom: 2em;
	position: absolute;
	color: #4a4a4a;
}

a.wpc-button:hover {
	border: none;
}

a.wpc-button .wpc-active:before {
	content: "";
	width: 50px;
	height: 50px;
	background-image: url("./img/spinner.png");
	background-image: none, url("./img/spinner.svg");
	background-repeat: no-repeat;
	background-repeat: no-repeat;
	background-position: center center;
	background-size: 32px 32px;
	background-color: transparent;
	padding: 0;
	display: block;
	position: absolute;
	top: -12.5px;
	left: calc(-100% + 12.5px);
	animation: spin 3s linear 0 infinite normal;
}

@keyframes spin {
	from {
		-ms-transform: rotate(0deg);
		/* IE 9 */
		-webkit-transform: rotate(0deg);
		/* Safari */
		transform: rotate(0deg);
	}
	to {
		-ms-transform: rotate(360deg);
		/* IE 9 */
		-webkit-transform: rotate(360deg);
		/* Safari */
		transform: rotate(360deg);
	}
}

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	border: 1px solid;
	border-color: #ccc #ccc #bbb;
	border-radius: 3px;
	background: #e6e6e6;
	color: rgba(0, 0, 0, 0.8);
	font-size: 13.5px;
	font-size: 0.84375rem;
	line-height: 1;
	padding: .6em 1em .4em;
}

button:hover,
input[type="button"]:hover,
input[type="reset"]:hover,
input[type="submit"]:hover {
	border-color: #ccc #bbb #aaa;
}

button:active, button:focus,
input[type="button"]:active,
input[type="button"]:focus,
input[type="reset"]:active,
input[type="reset"]:focus,
input[type="submit"]:active,
input[type="submit"]:focus {
	border-color: #aaa #bbb #bbb;
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
	color: #666;
	border: 1px solid #ccc;
	border-radius: 3px;
	padding: 3px;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
input[type="number"]:focus,
input[type="tel"]:focus,
input[type="range"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="week"]:focus,
input[type="time"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="color"]:focus,
textarea:focus {
	color: #111;
}

select {
	border: 1px solid #ccc;
}

textarea {
	width: 100%;
}

