# Navigation
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Links
--------------------------------------------------------------*/
a {
	color: #404040;
	text-decoration: none;
	border-bottom: 2.5px solid currentcolor;
}

a:visited {
	color: currentcolor;
}

a:hover, a:focus, a:active {
	color: #D96C70;
	border-bottom: 3px solid #D96C70;
	opacity: .8;
}

a:focus {
	outline: thin dotted;
}

a:hover, a:active {
	outline: 0;
}

.post-edit-link {
	margin: 1em 0;
	background-color: orange;
	color: cyan;
	padding: 1em;
	border: none;
}

.post-edit-link:hover {
	border: none;
	color: white;
	background-color: cyan;
}

.back2modulo {
	font-family: "Rubik", sans;
	font-size: 70%;
	border: none;
	color: #999;
	line-height: 1.3;
	display: inline-block;
}

/*--------------------------------------------------------------
## Menus
--------------------------------------------------------------*/
.main-navigation {
	clear: both;
	display: block;
	width: 100%;
	position: relative;
	z-index: 999;
}

.main-navigation ul.menu {
	font-family: "Rubik", sans;
	margin-top: 1px;
	background: white;
	width: 90vw;
	max-width: 320px;
	height: 100vh;
	padding: 0 0 80px;
	overflow-y: scroll;
	border-right: 1px solid #ccc;
	box-shadow: 4px 8px 8px 0 rgba(0, 0, 0, 0.32);
	font-size: .75em;
}

@media screen and (min-width: 600px) {
	.main-navigation ul.menu {
		width: 400px;
		max-width: none;
	}
}

.main-navigation ul.menu::-webkit-scrollbar {
	width: .75em;
}

.main-navigation ul.menu::-webkit-scrollbar-track {
	-webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

.main-navigation ul.menu::-webkit-scrollbar-thumb {
	background-color: #4a4a4a;
	outline: 0;
}

.main-navigation ul.menu.fixed {
	top: 0;
	position: fixed;
	z-index: 999;
}

.main-navigation ul {
	display: none;
	list-style: disc;
	margin: 0;
	padding-left: 0;
}

.main-navigation ul ul {
	display: none;
	position: relative;
	top: 100%;
	z-index: 99999;
	background-color: #f4f4f4;
}

.main-navigation ul ul ul {
	top: 100%;
}

.main-navigation ul ul li {
	padding-left: 1.25em;
	position: relative;
}

.main-navigation li {
	position: relative;
	display: block;
}

.main-navigation li.current-item a {
	background: #f4f4f4;
	font-weight: 500;
}

.main-navigation a {
	display: block;
	text-decoration: none;
	padding: .5em;
	border: none;
	line-height: 1.4;
	border-bottom: 1px solid #e6e6e6;
}

@media screen and (min-width: 600px) {
	.main-navigation a {
		padding: 1em;
	}
}

.main-navigation a:hover {
	color: #4a4a4a;
	opacity: .6;
}

.main-navigation .current_page_item > a,
.main-navigation .current-menu-item > a,
.main-navigation .current_page_ancestor > a,
.main-navigation .current-menu-ancestor > a {
	color: #D96C70;
}

.main-navigation .menu-item-has-children > a,
.main-navigation .page_item_has_children > a {
	padding-right: 2.5em;
}

.main-navigation a.home {
	background-image: url("./img/home-icon-dark.png");
	background-image: none, url("./img/home-icon-dark.svg");
	background-repeat: no-repeat;
	background-position: 1em center;
	padding-left: 3em;
	background-size: 1.25em;
}

.main-navigation .modulo-item {
	background-image: url("./img/flecha.png");
	background-image: none, url("./img/flecha.svg");
	background-repeat: no-repeat;
	background-position: 1em center;
	padding-left: 3em;
}

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
	display: block;
}

button.dropdown-toggle {
	position: absolute;
	right: .5em;
	top: .5em;
	height: 30px;
	width: 30px;
	padding: 0;
	background: inherit;
	border: none;
	color: #4a4a4a;
	text-align: center;
	-moz-border-radius: 50%;
	-webkit-border-radius: 50%;
	border-radius: 50%;
	font-weight: 700;
	font-size: 1em;
	line-height: 1;
}

button.dropdown-toggle:hover {
	background-color: #f4f4f4;
}

button.dropdown-toggle:active {
	background-color: #D96C70;
	color: white;
}

/* Toggle small menu and children on */
.toggled-on ul,
.sub-menu.toggled-on {
	display: block;
}

ul#footer-menu {
	list-style: none;
	margin: 2em 0;
	padding: 0;
	font-size: .75em;
	font-weight: 300;
	letter-spacing: .2px;
}

ul#footer-menu li {
	display: inline-block;
	margin: 0 .25em;
}

ul#footer-menu li a {
	border: none;
}

ul#footer-menu li:not(:last-child):after {
	content: "|";
	margin-left: 0.75em;
}

/*--------------------------------------------------------------
## Paginación
--------------------------------------------------------------*/
@media screen and (min-width: 900px) {
	.single-caso .ebook-nav {
		margin: 1em 0;
	}
}

.ebook-nav {
	height: 50px;
	margin: 2em auto;
	bottom: 0;
	left: 0;
	background: #eee;
	font-size: .8em;
	border: 2px solid #393A38;
	font-family: 'Rubik', Helvetica, Arial, sans-serif;
	overflow: hidden;
}

@media screen and (min-width: 900px) {
	.ebook-nav {
		height: 60px;
		min-width: 100%;
		margin-left: -3em;
		margin-right: -3em;
	}
}

.ebook-nav nav,
.ebook-nav .navigation,
.ebook-nav .nav-links {
	margin: 0 !important;
	padding: 0;
	height: 100%;
	width: 100%;
}

.ebook-nav .nav-links {
	display: flex;
	align-items: stretch;
}

.ebook-nav .nav-links span.meta-nav {
	display: block;
	font-size: .6em;
}

.ebook-nav .nav-links .nav-previous, .ebook-nav .nav-links .nav-next {
	width: 50%;
	margin-top: -1px;
	padding: 0;
	border: 0;
	-moz-border-radius: 0;
	-webkit-border-radius: 0;
	border-radius: 0;
	overflow: hidden;
}

.ebook-nav .nav-links .nav-previous .post-title, .ebook-nav .nav-links .nav-next .post-title {
	/* Required for text-overflow to do anything */
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
	display: block;
}

.ebook-nav .nav-links .nav-previous {
	margin-right: auto;
}

.ebook-nav .nav-links .nav-previous .arrow {
	float: left;
	margin-right: 4px;
}

.ebook-nav .nav-links .nav-next {
	margin-left: auto;
}

.ebook-nav .nav-links .nav-next .arrow {
	float: right;
	margin-left: 4px;
}

.ebook-nav a {
	border: none;
	display: block;
	padding: 1em .5em;
	height: 100%;
	outline: 0;
}

.ebook-nav a:hover {
	background: #D96C70;
	color: white;
	transition: .2s all;
	border: none;
	opacity: 1;
}

@media screen and (min-width: 900px) {
	.ebook-nav {
		height: 70px;
		font-size: .9em;
	}
	.ebook-nav .nav-links span.meta-nav {
		display: block;
		font-size: .7em;
	}
	.ebook-nav .nav-links .nav-previous, .ebook-nav .nav-links .nav-next {
		width: 48%;
		padding: 0;
	}
	.ebook-nav .nav-links .nav-previous a, .ebook-nav .nav-links .nav-next a {
		padding: 1.25em 1em .5em;
	}
}

.comment-navigation,
.posts-navigation,
.post-navigation {
	display: block;
	width: 100%;
}

.site-main .comment-navigation, .site-main
.posts-navigation, .site-main
.post-navigation {
