	margin-bottom: 1em;
}

.widget li a.rsswidget {
	padding-right: .5em;
	font-size: 1em;
	line-height: 1.4em;
}

.rss-date,
.widget_rss cite {
	color: #333;
	font-size: .8em;
}

.rssSummary {
	padding: .5em 0;
	font-family: "Rubik", sans;
	font-size: .8em;
	line-height: 1.4em;
}

/* Search widget */
.widget_search input[type="search"] {
	width: 100%;
}

/* Calendar widget */
.widget_calendar {
	font-weight: 400;
}

.widget_calendar caption {
	padding: .5em 0;
	font-size: 1em;
	font-weight: 500;
	text-align: left;
}

.widget_calendar thead {
	background: transparent;
	color: black;
}

.widget_calendar thead th {
	border-bottom-width: 2px;
}

.widget_calendar td {
	padding: .2em;
	font-size: .8em;
	text-align: center;
	background: white;
	border: 1px solid #cccccc;
}

.widget_calendar th {
	border: none;
	text-align: center;
}

.widget_calendar .pad {
	background: #eaeaea;
}

.widget_calendar a {
	font-weight: 900;
	text-decoration: none;
}

/* Text widget */
.textwidget {
	font-size: 80%;
	line-height: 1.3em;
}

/*------------------------------------------
** Estilos para la search bar
------------------------------------------*/
li.flexieduca-search, .widget_search {
	padding: 0;
	width: 100%;
	flex: 0 1 auto;
}

.widget_search {
	border: none;
}

.search-form {
	position: relative;
}

.search-form:after {
	content: "";
	background-image: url("./img/lupa.png");
	background-image: none, url("./img/lupa.svg");
	background-repeat: no-repeat;
	width: 20px;
	height: 20px;
	background-repeat: no-repeat;
	display: inline-block;
	position: absolute;
	top: calc(50% - 10px);
	right: 1em;
	z-index: 1;
	line-height: 2;
	font-size: 100%;
}

.search-form label {
	height: 40px;
	width: 80%;
	flex-grow: 1;
	position: relative;
	color: #C1B9A0;
}

.search-form .search-field {
	width: 100%;
	height: 40px;
	border: 0 none;
	flex-grow: 1;
	outline: 0;
	border: 2px solid #A39C87;
	-moz-border-radius: 4px;
	-webkit-border-radius: 4px;
	border-radius: 4px;
	padding-right: 3em;
	padding-left: 1em;
	-webkit-appearance: none;
	background: #DBD6C6;
	font-family: Helvetica, Arial, sans-serif;
	font-size: 95%;
	font-weight: 400;
	color: #A39C87;
	-webkit-transition: all .5s;
	transition: all .5s;
}

.search-form .search-field:focus {
	width: 100%;
	outline-color: #DDBE59;
	font-style: normal;
	border: 1px solid #C1B9A0;
	background: #fff;
	color: #333;
}

.search-form .search-field::-webkit-input-placeholder {
	outline: 0;
	border: 0 none;
	font-weight: normal;
	font-style: italic;
	color: #A39C87;
}

.search-form .search-field:-moz-placeholder {
	outline: 0;
	border: 0 none;
	font-weight: normal;
	font-style: italic;
	color: #A39C87;
}

.search-form .search-field:-ms-input-placeholder {
	color: #DDBE59;
	outline: 0;
	border: 0 none;
	font-weight: normal;
	font-style: italic;
	color: #A39C87;
}

.search-form .search-submit {
	display: none;
}

